<template>
  <el-menu
    default-active="1-4-1"
    class="el-menu-vertical-demo"
    @open="handleOpen"
    @close="handleClose"
    :collapse="isCollapse"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b">
    <h3>{{isCollapse? '後台':'通用後台管理系統'}}</h3>
    <el-menu-item @click="clickMenu(item)"  v-for="item in noChildren" :key="item.name" :index="item.name">
      <i :class="`el-icon-${item.icon}`"></i>
      <span slot="title">{{ item.label }}</span>
    </el-menu-item>
    <el-submenu v-for="item in hasChildren" :key="item.label" :index="item.label">
      <template slot="title">
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">{{ item.label }}</span>
      </template>
      <el-menu-item-group v-for="subItem in item.children" :key="subItem.path">
        <el-menu-item @click="clickMenu(subItem)" :index="subItem.path">{{ subItem.label }}</el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>

<script>
import Cookie from "js-cookie"
export default {
  data() {
    return {
    //   isCollapse: false,
      // menuData: [
      //   {
      //     path: "/",
      //     name: "home",
      //     label: "首页",
      //     icon: "s-home",
      //     url: "Home/Home",
      //   },
      //   {
      //     path: "/mall",
      //     name: "mall",
      //     label: "商品管理",
      //     icon: "s-mall",
      //     url: "MallManage/MallManage",
      //   },
      //   {
      //     path: "/user",
      //     name: "user",
      //     label: "用戶管理",
      //     icon: "s-user",
      //     url: "UserManage/UserManage",
      //   },
      //   {
      //     label: "其他",
      //     icon: "lcoation",
      //     children: [
      //       {
      //         path: "/page1",
      //         name: "page1",
      //         label: "首页1",
      //         icon: "setting",
      //         url: "Other/PageOne",
      //       },
      //       {
      //         path: "/page2",
      //         name: "page2",
      //         label: "首页2",
      //         icon: "setting",
      //         url: "Other/PageTwo",
      //       },
      //     ],
      //   },
      // ]
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    //點擊菜單
    clickMenu(item){
        console.log(item)
        // 當頁面的路由與跳轉的路由不一致才允許跳轉 (處理跳轉報錯的問題)
        if(this.$route.path !== item.path && !(this.$route.path === '/home' && (item.path === '/')) ){ //這裡的$route表示當前頁面的路由
            this.$router.push(item.path) //這裡的$router表示的是整個router實例
        }
        this.$store.commit('selecMenu',item)
    }
  },
  computed: {
    //沒有子菜單
    noChildren() {
      return this.menuData.filter(item => !item.children)
    },
    //有子菜單
    hasChildren() {
      return this.menuData.filter(item => item.children)
    },
    menuData(){
      //判斷當前數據，如果緩存中沒有，當前store中去獲取。
      return JSON.parse(Cookie.get('menu')) || this.$store.state.tab.menu  //如果緩存中存在就直接讀去Cookie.get('menu')數據，如果不存在就去state的中的數據去讀取(this.$store.state.tab.menu)
      
    },
    isCollapse(){
        return this.$store.state.tab.isCollapse
    }
  }
}
</script>

<style lang="less" scope>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.el-menu {
    border-right: none;
    height: 100vh;
    h3{
        color: #fff;
        line-height: 48px;
        text-align: center;
        font-size: 16px;
        font-weight: 400px;
    }
}
</style>